body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #C4A484;
    overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

h1{
  text-align: center;
  margin-bottom: 40px;
}

#root{
}

#calculator{
  position: relative;
  border-radius: 15px;
  background-color: #878787;
  max-width: 450px;
  padding: 32px 16px;
  margin: 0 auto;
  z-index: 10;
  box-shadow: 3px 3px 10px rgb(0 0 0 / 0.5);
}

@font-face {
  font-family:"Dot Matrix";
  src: url("./fonts/dot_matrix/dotmat.eot?") format("eot"),
  url("./fonts/dot_matrix/dotmat.woff") format("woff"),
  url("./fonts/dot_matrix/dotmat.ttf") format("truetype"),
  url("./fonts/dot_matrix/dotmat.svg#DotMatrixNormal") format("svg");
  font-weight:normal;
  font-style:normal;
}

#display{
  background-color: #000;
  padding: 12px;
  padding-bottom: 6px;
  margin-bottom: 24px;
  font-size: 2.4em;
  color: #ffffb7;
  font-family: "Dot Matrix";
}

#button-container{
  margin: 0 auto;
  display: grid;
  grid-template-columns: 3fr 3fr 3fr 3fr 2fr;
  grid-template-rows: repeat(4, 1fr);
  max-width: 450px;
  height: 240px;
  gap: 8px;
}

.button{
  cursor: pointer;
  border: 1px solid #3A3B3C;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2em;
  background-color: #fff;
}

.blank{
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2em;
}

#equals{
  grid-column: 5;
  grid-row: 3 / 5;
}

#clear{
  grid-column: 5;
  grid-row: 1 / 3;
}

#results{
  background-color: #fff;
  max-width: 350px;
  padding: 12px;
  margin: 0 auto;
  z-index: 1;
  box-shadow: 2px 2px 5px rgb(0 0 0 / 0.3);
}

#results ul{
  list-style-type: none;
  line-height: 2em;
}
